<template>
  <div id="container"></div>
  <video id="video" playsinline style="
          -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
          visibility: hidden;
          width: auto;
          height: auto;
          ">
  </video>
</template>

<script>
import {setupCamera} from '@/core/video/video'
import {predict, setupModel} from '@/core/tensorflow/facelandmark'
import {RectLash, ImageLash, getScale} from '@/core/material/material'
import Konva from "konva";

export default {
  name: "Board",
  async mounted() {
    // 1. setup camera, which should pass stream to video tag
    let video = await setupCamera("video");
    await setupModel();

    // 2. set up konva stage and layer, which should display camera video on container
    var width = video.videoWidth;
    var height = video.videoHeight;

    var stage = new Konva.Stage({
      container: 'container',
      width: width,
      height: height,
    });

    var layer = new Konva.Layer({
      // just flip whole layer, all elements in the layer will flip
      offsetX: width,
      scaleX: -1,
    });
    stage.add(layer);

    var image = new Konva.Image({
      image: video,
      width: video.videoWidth,
      height: video.videoHeight,
    });
    layer.add(image);
    console.log(image);

    let rightMaterial = this.newRightMaterial();
    layer.add(rightMaterial);

    let leftMaterial = this.newLeftMaterial();
    layer.add(leftMaterial);


    // 3. animate video, and predict video frame
    var anim = new Konva.Animation(async function () {
      // 4. get key points
      let keypoints = await predict(video);
      console.log(keypoints[0])
      if (keypoints.length > 0) {
        let anno = keypoints[0].annotations;
        let rightScale = getScale([rightMaterial.meta.right, rightMaterial.meta.left],
            [anno.rightEyeUpper0[6], anno.rightEyeUpper2[0]])
        let leftScale = getScale([leftMaterial.meta.right, leftMaterial.meta.left],
            [anno.leftEyeUpper0[6], anno.leftEyeUpper2[0]])
        console.log(rightScale)
        rightMaterial.setAttrs({
          x: anno.rightEyeUpper0[6][0],
          y: anno.rightEyeUpper0[6][1],
          rotation: 2,
          // scale will scale all position, include offset and width, so we use original offset is ok
          offsetX: rightMaterial.meta.right[0],
          offsetY: rightMaterial.meta.right[1],
          scale: {
            x: rightScale,
            y: rightScale,
          }
        });

        leftMaterial.setAttrs({
          x: anno.leftEyeUpper0[6][0],
          y: anno.leftEyeUpper0[6][1],
          rotation: -2,
          offsetX: leftMaterial.meta.right[0],
          offsetY: leftMaterial.meta.right[1],
          // offsetY: 10,
          scale: {
            x: leftScale,
            y: leftScale,
          }
        });
      }
    }, layer);
    video.play();
    anim.start();

  },
  methods: {
    newRightMaterial() {
      let imageObj = new Image()
      imageObj.src = 'jiemao.png'
      let meta = {
        "right": [611, 272],
          "left": [282, 293],
          "size": [900, 600]
      };
      return new ImageLash(imageObj, meta)
    },
    newLeftMaterial() {
      let imageObj = new Image()
      imageObj.src = 'jiemao1.png'
      let meta = {
        "right": [900-611, 272],
        "left": [900-282, 293],
        "size": [900, 600]
      };
      return new ImageLash(imageObj, meta)
    },
  }
}
</script>

