import Konva from "konva";

export function getScale(materialPoints, eyePoints) {
    console.log(materialPoints);
    console.log(eyePoints);
    return getLength(eyePoints) / getLength(materialPoints)
}

export class RectLash extends Konva.Rect {
    constructor(width, height, keypoint) {
        let config = {
            width: width,
            height: height,
            fill: 'red',
            stroke: 'black',
            strokeWidth: 2,
        }
        super(config);
        this.keypoint = keypoint
    }
}

export class ImageLash extends Konva.Image {
    constructor(image, imageMeta) {
        let config = {
            width: imageMeta.size[0],
            height: imageMeta.size[1],
            image: image,
        }
        super(config);
        this.meta = imageMeta;
    }
}


// get line degree
export function getRotation(eyePoints) {
    let x = eyePoints[0][0] - eyePoints[1][0];
    let y = eyePoints[0][1] - eyePoints[1][1];
    let degree = Math.atan2(y, x) * 180 / Math.PI;
    return degree;
}

// points[0] = [x, y]
// points[1] = [x, y]
function getLength(points) {
    return Math.sqrt(Math.pow(points[0][0] - points[1][0], 2)
        + Math.pow(points[0][1] - points[1][1], 2));
}
